import { skills as skillsStyles } from "./styles.module.scss";

type Props = {
  readonly skills: string[];
};

export const isValidSkills = (node: {
  readonly skills: readonly (string | null)[] | null;
}): node is Props => Boolean(node.skills?.every(Boolean));

export const Skills = ({ skills }: Props) => (
  <ul className={skillsStyles}>
    {skills.map((s) => (
      <li key={s}>{s}</li>
    ))}
  </ul>
);
