import * as styles from "./styles.module.scss";

type Props = {
  school: string;
  degrees: Array<{
    degree: string;
    program: string;
    year: string;
  }>;
};
export const Education = ({ school, degrees }: Props) => (
  <>
    <h3 className={styles.school}>{school}</h3>
    {degrees.map(({ degree, program, year }, idx, arr) => (
      <h4
        key={program}
        className={`${styles.degree} ${
          idx === arr.length - 1 ? styles.last : ""
        }`}
      >
        {degree} - {program} {year}
      </h4>
    ))}
  </>
);
