import * as styles from "./desc.module.scss";

type JobDesc = {
  project: string;
  longName: string | null;
  intro: string;
  contribution: string[];
  links: Array<{ url: string; str: string }>;
};
type JobDescNode = {
  readonly [prop in keyof JobDesc]: JobDesc[prop] extends string | null
    ? JobDesc[prop] | null
    : prop extends "contribution"
    ? readonly (string | null)[] | null
    : ReadonlyArray<{
        url: string | null;
        str: string | null;
      } | null> | null;
};

export const isValidJobDesc = (node: JobDescNode): node is JobDesc =>
  Boolean(node.project) &&
  Boolean(node.intro) &&
  Boolean(node.contribution) &&
  Boolean(node.links);

export const JobDesc = ({
  project,
  longName,
  intro,
  contribution,
  links,
}: JobDesc) => (
  <div className={styles.workDesc}>
    <h2 className={styles.projectName}>{project}</h2>
    {longName && <h3 className={styles.longName}>{longName}</h3>}
    <hr className={styles.projectUnderline} />

    <p className={styles.projectIntro}>{intro}</p>

    <h4 className={styles.contribHeader}>What I did</h4>
    {contribution.map((p) => (
      <p className={styles.contribItem} key={p}>
        {p}
      </p>
    ))}
    {links.length === 0 ? null : (
      <>
        <h4 className={styles.linksHeader}>Links</h4>
        <ul className="styles.projectLinks">
          {links.map(({ url, str }) => (
            <li className="styles.prLink" key={project}>
              <a href={url}>{str}</a>
            </li>
          ))}
        </ul>
      </>
    )}
  </div>
);
