import { Link, graphql, useStaticQuery } from 'gatsby';

import * as styles from './styles.module.scss';

export const getPageName = (componentName: string) => {
  const name = componentName?.replace(/[A-Z]/g, ' $&').match(/Component\/([\w ]+)/)?.[1];
  return `${name?.[0].toUpperCase()}${name?.slice(1)}`;
};

const excludedPages = (path: string) =>
  !(path === '/' || /.*layout.*/.test(path) || /.*definitions.*/.test(path) || /.*404.*/.test(path));

const Navigation = () => {
  const { allSitePage: {edges: pages} } = useStaticQuery<Queries.NavigationQuery>(query);
  return (
  <div id="nav">
    <ul className={styles.navigation}>
      <li>
        <Link to="/">Home</Link>
      </li>
      {pages
        .filter(({ node: { path } }) => excludedPages(path))
        .filter(({ node: { internalComponentName: n }}) => Boolean(n))
        .map(({ node: { path, internalComponentName: name } }) => name && (
          <li key={path}>
            <Link to={path}>{getPageName(name)}</Link>
          </li>
        ))}
    </ul>
  </div>
)};

export const query = graphql`
  query Navigation {
    allSitePage {
      edges {
        node {
          path
          internalComponentName
        }
      }
    }
  }
`

export default Navigation;
