import * as styles from "./styles.module.scss";
export * from "./Desc";

type Props = {
  employer: string;
  location: string;
  title: string;
  start: string;
  end: string;
  duties: string[];
};
type Node = {
  readonly [prop in keyof Props]: Props[prop] extends string
    ? Props[prop] | null
    : readonly (string | null)[] | null;
};

export const isValidJob = (node: Node): node is Props =>
  Boolean(node.employer) &&
  Boolean(node.location) &&
  Boolean(node.title) &&
  Boolean(node.start) &&
  Boolean(node.end);

export const Job = ({
  employer,
  location,
  title,
  start,
  end,
  duties,
}: Props) => (
  <div className={styles.job}>
    <h2 className={styles.title}>{title}</h2>
    <h3 className={styles.employer}>{employer}</h3>
    <h4 className={styles.dates}>
      {start} - {end}
    </h4>
    <h4 className={styles.location}>{location}</h4>
    <ul className={styles.duties}>
      {duties.map((d) => (
        <li
          key={d}
          dangerouslySetInnerHTML={{
            __html: d.replace(
              /<(\w+)>([\w\W]+?)<\/\w+>/g,
              '<span class="$1">$2</span>'
            ),
          }}
        />
      ))}
    </ul>
  </div>
);
