import { useState, ReactNode } from "react";

import { useResponsive } from "hooks";

import * as styles from "./style.module.scss";

type Props = {
  style?: Record<string, string>;
  title: string;
  children: ReactNode;
};
export const DropDown = ({ style, title, children }: Props) => {
  const [hidden, setHidden] = useState<boolean>(true);
  const size = useResponsive({ mobileWidth: 540, wideWidth: 960 });

  return (
    <div
      style={style}
      className={`${styles.dropDown} ${hidden ? styles.hidden : ""} ${
        size !== "mobile" ? "disabled" : ""
      }`}
    >
      <h3 className={styles.title} onClick={() => setHidden((v) => !v)}>
        {title} <span className={styles.arrow}>&#x25B2;</span>
      </h3>
      <div className={styles.cont}>{children}</div>
    </div>
  );
};
