import { useEffect, useRef, useCallback, useState } from 'react';

export type Size = 'mobile' | 'narrow' | 'wide';
type WidthConfig = {
  mobileWidth: number;
  wideWidth: number;
};
export const useResponsive = ({ mobileWidth, wideWidth}: WidthConfig) => {
  const isResizing = useRef<boolean>(false);
  const [size, setSize] = useState<Size>('mobile');

  const updateView = useCallback(() => {
    const width = document.body.clientWidth;

    if (
      (size === 'mobile' && width > mobileWidth) ||
      (size === 'narrow' && !(width < wideWidth && width > mobileWidth)) ||
      (size === 'wide' && width < wideWidth)
    ) {
      if (width <= mobileWidth) {
        setSize('mobile');
      } else if (width >= wideWidth) {
        setSize('wide');
      } else {
        setSize('narrow');
      }

      isResizing.current = false;
    }
  }, [mobileWidth, wideWidth, size, setSize]);

  useEffect(() => {
    window.addEventListener('resize', () => {
      if (!isResizing.current) {
        isResizing.current = true;
        window.requestAnimationFrame(updateView);
      }
    });
    updateView();

    return () => {
      window.removeEventListener('resize', updateView);
    }
  });

  return size;
}